.Create {
  padding-bottom: 3.5em;
}
@media (max-width: 31.999em) {
  .Create .Donut {
    padding: 0;
  }
}
@media (min-width: 32em) {
  .Create {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    min-height: calc(100vh - 4.56em);
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    padding-bottom: 0;
  }
  .Create .Donut {
    height: calc(100vh - 4.56em);
    width: 66.67%;
  }
  .Create .Donut svg {
    max-height: 100%;
    max-width: 100%;
  }
  .Create .DonutForm {
    width: 33.33%
  }
}



