/**
 * Admin.
 *
 * Styles for the *super secret* admin page
 */
.Admin {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 2;
      flex-grow: 2;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 1rem;
  position: relative;
}

/** Loading state */

.Admin.is-loading > * {
  opacity: .33;
}
.Admin.is-loading:before {
  content: '👹';
  font-size: 600%;
  height: 1em;
  left: 50%;
  line-height: 1;
  margin: -.5em 0 0 -.5em;
  position: absolute;
  top: 50%;
  width: 1em;
  z-index: 2;
}
.Admin.is-loading:after {
  -webkit-animation: isLoading 2.5s linear infinite;
          animation: isLoading 2.5s linear infinite;
  content: '🍩';
  font-size: 300%;
  height: 1em;
  left: 50%;
  line-height: 1;
  margin: -3em 0 0 -.5em;
  position: absolute;
  top: 50%;
  -webkit-transform-origin: .5em 3em;
      -ms-transform-origin: .5em 3em;
          transform-origin: .5em 3em;
  width: 1em;
  z-index: 2;
}
@-webkit-keyframes isLoading {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
@keyframes isLoading {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

/** Error message */

.Admin-error-message {
  background: gold;
  color: black;
  display: inline-block;
  left: 45%;
  padding: .5rem;
  position: absolute;
  -webkit-transform: rotate(-6deg);
      -ms-transform: rotate(-6deg);
          transform: rotate(-6deg);
  top: 1rem;
  z-index: 2;
}

/** Button */

.Admin-button {
  -ms-flex-item-align: center;
      align-self: center;
  background: firebrick;
  border: none;
  border-radius: 50%;
  -webkit-box-shadow:
    .5px 1px black,
    1px 2px black,
    1.5px 3px black,
    2px 4px black,
    2.5px 5px black,
    3px 6px black;
          box-shadow:
    .5px 1px black,
    1px 2px black,
    1.5px 3px black,
    2px 4px black,
    2.5px 5px black,
    3px 6px black;
  color: white;
  cursor: pointer;
  display: inline-block;
  font-family: inherit;
  font-size: 200%;
  font-weight: 700;
  height: 10em;
  margin: 0 0 1rem;
  padding: 0;
  -webkit-transition-duration: 80ms;
       -o-transition-duration: 80ms;
          transition-duration: 80ms;
  -webkit-transition-property: background, text-shadow, -webkit-box-shadow, -webkit-transform;
  transition-property: background, text-shadow, -webkit-box-shadow, -webkit-transform;
  -o-transition-property: background, box-shadow, text-shadow, transform;
  transition-property: background, box-shadow, text-shadow, transform;
  transition-property: background, box-shadow, text-shadow, transform, -webkit-box-shadow, -webkit-transform;
  text-align: center;
  text-shadow:
    -0.5px -1px black,
    -1px -2px black;
  width: 10em;
}
.Admin-button:focus,
.Admin-button:hover {
  background-color: crimson;
  -webkit-transform: translate(-1px, -1px);
      -ms-transform: translate(-1px, -1px);
          transform: translate(-1px, -1px);
}
.Admin-button:active {
  background-color: red;
  -webkit-box-shadow:
    -.5px -1px black,
    -1px -2px black,
    -1.5px -3px black,
    -2px -4px black,
    -2.5px -5px black,
    -3px -6px black,
    -3.5px -7px black,
    -4px -8px black,
    -4.5px -9px black,
    -5px -10px black;
          box-shadow:
    -.5px -1px black,
    -1px -2px black,
    -1.5px -3px black,
    -2px -4px black,
    -2.5px -5px black,
    -3px -6px black,
    -3.5px -7px black,
    -4px -8px black,
    -4.5px -9px black,
    -5px -10px black;
  text-shadow:
    -0.5px -1px black,
    -1px -2px black;
  -webkit-transform: translate(2px, 2px);
      -ms-transform: translate(2px, 2px);
          transform: translate(2px, 2px);
}

