}

/** Spray button */
.Admin-spray {
  background: orangered;
  border: none;
  border-radius: .25rem;
  color: white;
  cursor: pointer;
  display: inline-block;
  font-family: inherit;
  font-size: 100%;
  font-weight: 700;
  line-height: inherit;
  padding: 1em;
}


