/**
 * Donut viewer.
 * @TODO Rename
 */

.DonutViewer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.DonutViewer-TransitionGroup {
  position: relative;
}

.DonutViewer-item {
  -webkit-animation: 6s ease-in-out forwards donutViewerSlideUp;
          animation: 6s ease-in-out forwards donutViewerSlideUp;
  height: 0;
  padding-bottom: 50%;
  position: absolute;
  text-align: center;
}
.DonutViewer-item .Donut {
  margin-top: -50%;
}
@media (min-width: 32em) {
  .DonutViewer-item {
    width: 50%
  }
  .DonutViewer-item-odd {
    right: 0;
  }
}

@-webkit-keyframes donutViewerSlideUp {
  from {
    -webkit-transform: translateY(10vh);
            transform: translateY(10vh);
  }
  to {
    -webkit-transform: translateY(-30vh);
            transform: translateY(-30vh);
  }
}

@keyframes donutViewerSlideUp {
  from {
    -webkit-transform: translateY(10vh);
            transform: translateY(10vh);
  }
  to {
    -webkit-transform: translateY(-30vh);
            transform: translateY(-30vh);
  }
}



