/**
 * Donuts
 */

.Donuts {
  padding-top: 1em;
}
.Donuts h1 {
  margin: 0 0 1.5rem;
}

.Donuts-link {
  background: rgba(255, 255, 255, 0.1);
  border: .125em solid rgba(255, 255, 255, .75);
  border-radius: .25rem;
  color: inherit;
  display: inline-block;
  font-weight: 700;
  padding: 1em 1.25em;
  text-decoration: none;
}
@media (min-width: 32em) {
  .Donuts-link {
    font-size: 125%;
  }
}

.Donuts-link:active {
  background: #fff;
  border-color: rgba(35, 64, 105, .95);
  color: #234069;
}
.Donuts-link:focus,
.Donuts-link:hover {
  background: #fff;
  border-color: rgba(35, 64, 105, .85);
  color: #234069;
}

/** Empty state: no donuts exist */
.Donuts.is-empty {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 2;
      flex-grow: 2;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
}

.Donuts.is-empty a:first-of-type {
  display: block;
  margin-bottom: 1rem;
}





